/* ---------------Home Page-------------- */

#splash_image {
    height: 700px;
    background: rgba(253, 253, 253, 0.048) url(./Images/Splash_Image.jpg);
    background-blend-mode: darken;
    background-repeat: no-repeat;
    background-position: center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#splashTitleBox {
    background: rgb(253, 253, 253, 0.8);
}

#search_bar {
    background-color: white;
    width: 35%;
    border-radius: 50px;
    height: 2.5rem;
    box-shadow: 0px 0px 8px #a29f9f;
    display: flex;
    align-items: center;
    margin: 0 auto;
}

#searchBar_icon {
    margin-left: 10px;
    color: #4E613A;
}

#search_bar input {
    background-color: transparent;
    border: none;
    height: 100%;
    font-family: 'Titillium Web', sans-serif;;
    font-size: 1.25rem;
    width: 100%;
    margin-left: 5px;
}

#search_bar input:focus {
    outline: none;
}

#results_list {
    width: 35%;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 8px #ddd;
    border-radius: 10px;
    margin-top: 1rem;
    max-height: 200px;
    /* overflow: scroll; */
    align-items: center;
    margin: 0 auto;
}

#search_result {
    font-family: 'Titillium Web', sans-serif;
    
}

#search_result:hover {
    cursor: pointer;
    text-decoration: underline;
}

#tag_line {
    font-family: 'Titillium Web', sans-serif;
    font-size: large;
    font-weight: bold;
    color: #FE7134;
}

#register_tagline{
    font-family: 'Titillium Web', sans-serif;
    font-size: 20px;
    color: #4E613A;
    font-weight: bold;
}

#register_tagline:hover{
    color: #FE7134;
    font-size: 22px;
    font-weight: bold;
}

