/* ---stars---- */

#fastars {
    color: #FE7134;
}

.rating {
    /* width: 226px; */
    /* margin: 0 auto 1em; */
    font-size: 30px;
    /* overflow:hidden; */
}
.rating input {
/* float: right; */
opacity: 0;
/* position: absolute; */
}
.rating a,
.rating label {
    /* float:right; */
    color: #aaa;
    text-decoration: none;
    -webkit-transition: color .4s;
    -moz-transition: color .4s;
    -o-transition: color .4s;
    transition: color .4s;
}
.rating label:hover ~ label,
.rating input:focus ~ label,
.rating label:hover,
.rating a:hover,
.rating a:hover ~ a,
.rating a:focus,
.rating a:focus ~ a		{
    color: #FE7134;
    cursor: pointer;
}
.rating2 {
    direction: rtl;
}

